@import url("https://fonts.googleapis.com/css?family=Rajdhani:400,500,600,700&display=swap");

body {
  margin: 0;
  overflow: hidden;
  font-family: 'Rajdhani', Arial, sans-serif;
}

.App {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(135deg, #1aaf5d, #0d381a);
  background-size: cover;
  color: white;
  user-select: none;
}

.App::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(26, 175, 93, 0.3) 25%, transparent 25%) 0 0, 
              linear-gradient(225deg, rgba(13, 56, 26, 0.3) 25%, transparent 25%) 0 0,
              linear-gradient(315deg, rgba(26, 175, 93, 0.3) 25%, transparent 25%) 0 0,
              linear-gradient(45deg, rgba(13, 56, 26, 0.3) 25%, transparent 25%) 0 0;
  background-size: 100px 100px;
  z-index: -1;
  opacity: 0.6;
}

.top-menu {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 10px;
  background-color: rgba(10, 46, 21, 0.8); /* Slightly transparent to match the App background */
  box-shadow: 0 2px 4px rgba(20, 100, 30, 0.5); /* Reduced glow from top for subtlety */
}

.profile-area {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.profile-icon {
  width: 50px; /* Adjust as needed */
  height: 50px; /* Adjust as needed */
  margin-right: 10px;
}

.username {
  font-size: 18px; /* Adjust as needed */
  color: #14cc1e; /* Soft green text */
}

.buttons-container {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(10, 46, 21, 0.8); /* Consistent with .App */
  padding: 20px 0 30px;
  display: flex;
  justify-content: space-around;
  box-shadow: 0 -2px 4px rgba(20, 100, 30, 0.5); /* More subtle glow effect */
}

.button {
  flex: 1;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s;
}

.button img.icon {
  width: 60px;
  height: 60px;
}

.button:hover img.icon {
  transform: scale(1.1);
}

.button.active img.icon {
  filter: none; /* No brightness or contrast adjustments */
}

@media (max-width: 400px) {
  .buttons-container {
    padding: 10px 0 20px;
  }

  .button img.icon {
    width: 50px;
    height: 50px;
  }

  .button span {
    font-size: 12px;
  }
}
